.preloader {
    margin: 80px auto 55px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
  }
  
  .preloader__title {
    margin-top: 20px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #B6BCBF;
  }
  
  .preloader__spinner {
      display: block;
      width: 74px;
      height: 74px;
      border: 4px solid #444;
      border-bottom-color: #888;
      border-radius: 50%;
      margin: auto;
      animation: spin .75s infinite linear;
  }
  
  @keyframes spin {
      100% {
          transform: rotate(360deg);
      }
  }